

.catalog {
}
.catalog__container {
}

@media(min-width:768px){
    .catalog__inner {
        max-width: 768px;
        margin: 0 auto;
    }
}
.catalog__title {
    @extend %oswald-400;
    font-size: 36px;
    line-height: 60px;
    text-align: center;
    padding: 12px 0px 22px;
    margin-left: -23px;
}
@media(min-width:768px){
    .catalog__title {
        font-size: 60px;
        padding: 66px 0px 74px;
        text-align: left;
        margin-left: 0px;
    }
}
@media(min-width:768px){
    .catalog__list{
        display: flex;
        flex-basis: 780px;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-end;
    }
}
.catalog__item {
    border: 1px solid rgb(0, 0, 0);
    border-top: 1px solid #ededed;
    margin-left: -20px;
    margin-right: -20px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 25px;
}
@media(min-width:768px){
    .catalog__item {
        width: 324px;
        border-top: none;
        margin-left: 0px;
        margin-right: 0px;
        padding: 0 0 25px 0;
        background-image: linear-gradient(to bottom, #ffffff 13%, #f2f2f2 13%);
        margin-bottom: 58px;
    } 
}
.catalog__descr {
    border: 1px solid red;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
@media(min-width:768px){
    .catalog__descr {
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    } 
}
.catalog__table{
    border: 1px solid rgb(101, 46, 228);

}    
.catalog__table-title th{
    width: 75px;
    @extend %oswald-400;
    font-size: 16px;
    line-height: 20px;
    text-align: left;
    text-transform: uppercase;
    padding-bottom: 18px;
}
@media(min-width:768px){
    .catalog__table-title th{
        width: 126px;
        font-size: 20px;
        text-align: center;
        padding-bottom: 24px;
    }
}


.catalog__table-body {
    font-size: 12px;
    line-height: 13px;
    text-align: left;
}
.catalog__table-body td:last-child{
    text-align: right;
}
.catalog-btn, .more-btn {
    max-width: 506px;
    height: 40px;

    font-size: 16px;
    line-height: 20px;
    padding-top: 9px;
    margin: 0;
    position: relative;
    left: 50%;
    transform: translate(-50%);
}
@media(min-width:768px){
    .catalog-btn, .more-btn {
    width: 249px;
    }
}

// Блок №8, more
.catalog__plus{
    width: 30px;
    height: 30px;
    opacity: 0.3;
    margin: 25px auto;
    
}
.catalog__more-title {
    @extend %oswald-400;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    text-transform: uppercase;
    padding-bottom: 8px;
}
.catalog__more-text {
    font-size: 12px;
    line-height: 16px;
    text-align: center;
    padding-bottom: 22px;
}
.more-btn{
    color: #444;
    background-color: $bg-grey;
}
.more-btn:hover{
    background-color: #ebebeb;
}
.more-btn:active{
    color: rgba(68,68,68,.3);
    background-color: #ebebeb;
}
