.header__inner{
    border-bottom: 1px solid #ededed;
}


@media(min-width:768px){
    .header__inner{
        // border: 1px solid black;

        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 24px 30px 0 30px;
    }
}
@media(min-width:1440px){
    .header__inner{
        padding: 54px 110px 0px 110px;
    }
}


.header__burger-block{
    // border: 1px solid rgb(16, 214, 75);
    max-width: 280px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 11px auto 16px auto;
}
@media(min-width:768px){
    .header__burger-block{
        max-width: 768px;
        display: block;
        margin: 0px 0 0px 0;
    }
}
@media(min-width:768px){
    .header__burger-block{
        max-width: 1220px;
    }
}


.header__logo-img{
    width: 191px;
    height: 38px;
}
@media(min-width:768px){
    .header__logo-img{
        width: 173px;
        height: 58px;
    }
}
@media(min-width:1440px){
    .header__logo-img{
        width: 202px;
        height: 59px;
    }
}


// on/off
.header__btn-off{
    display: none;
    width: 18px;
    height: 18px;
}


//список

.header__list{
    display: none;
    text-align: center;
}
@media(min-width:768px){
    .header__list{
        // border: 1px solid rgb(58, 15, 251);
        display: flex;
        flex-direction: row;
    }
}


.header__item{
    display: block;
    @extend %oswald-400;
    text-transform: uppercase;
    font-size: 20px;
    line-height: 20px;
    color: #111111;
    padding-top: 24px;
    padding-bottom: 24px;
    border-top: 1px solid #e6e6e6;
}
.header__item:hover{
    opacity: 0.6;
    opacity: 0.6;
}
.header__item:focus{
    opacity: 0.3;
}
.header__item:active{
    opacity: 1;
}
@media(min-width:768px){
    .header__item{
        font-size: 18px;
        line-height: 24px;
        padding-top: 0px;
        padding-bottom: 0px;
        border-top: none;
    }
    .header__list li:not(:last-child){
        margin-right: 36px;
    }
    .header__item--active{
        border-bottom: 2px solid $bg-color;
        padding-bottom: 7px;
    }
}
@media (min-width:1440px) {
    .header__item{
        font-size: 20px;
        color: #ffffff;
    }
    .header__item:hover{
            opacity: 0.6;
    }
    .header__item:hover{
        opacity: 0.3;
    }
    .header__item--active{
        opacity: 1;
        border-bottom: 2px solid #ffffff;
    }
    
}



@media(min-width:768px){   //отключение бургера
    .header__burger-link{
        display: none;
    }
}













